body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}

nav {
    background-color: #333;
    position: fixed;
    width: 100%;
    padding: 0px;
   }
  
   .nav {
    content: '';
    display: block;
    clear: both;
  }
  
   .logo {
     margin: 8px;
     color: white;
   }
  
   .logo i {
     color: white;
     font-size: 38px;
     margin-right: 20px
   }
  
   .logo, .menu {
       display:inline-block;
   }
  
   .menu {
     float:right;
     margin: 15px;
   }
  
   .menu ul {
     list-style: none;
     padding-left: 0px;
     margin: 0;
   }
  
   .menu ul li {
       display: inline-block;
       margin-right: 20px;
   }
  
   .menu ul li a {
       text-decoration: none;
       color: white;
       font-size: 18px;
       padding: 18px 6px 18px 6px;
   }
  
   .menu ul li a:hover {
       background-color: grey;
       color:white;
       -webkit-transition: all 0.5s ease;
       transition: all 0.5s ease;
   }
header {
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/portfolio/static/media/hero.b503d781.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/portfolio/static/media/hero.b503d781.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  background-position: center;
  /* background-attachment: fixed;
    background-position-y: 20px;  */
}

header p {
  margin: 0;
}

.hero-container {
  text-align: center;
  padding-top: 60px;
  color: white;
}

.btn-work,
.btn-contact {
  font-weight: bold;
  border-radius: 20px;
  text-decoration: none;
  padding: 10px 25px;
  display: inline-block;
  font-size: 17px;
}

.btn-work {
  background-color: #85af4b;
  color: white;
  margin: 15px 10px;
}

.btn-work:hover {
  background-color: white;
  color: #85af4b;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.btn-contact {
  background-color: white;
  color: #85af4b;
}

.btn-contact:hover {
  background-color: #85af4b;
  color: white;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

@media only screen and (max-width: 900px) {
  .header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
.about-me {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  
  .about-me:after {
    content:'';
    display: block;
    clear: both;
  }
  
  .about-me h2 {
    color: #333;
    text-align: center;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 30px;
  }
  
  .about-me h2:after {
    content: '';
    position: absolute;
    width: 80px;
    height: 2px;
    margin-left: -40px;
    left: 50%;
    bottom: 0px;
    background-color: #85af4b
  }
  
  .about-me .col-left {
    width: 50%;
    float: left;
    padding-left: 5%;
    padding-right: 5%;
  }
  
  .about-me .col-right {
    width: 50%;
    float: right;
    text-align: justify;
    padding-right: 5%
  }
  
  .about-me a {
    background-color: #85af4b;
    text-decoration: none;
    color: white;
    padding: 10px 25px;
    border-radius: 20px;
    font-size: 17px;
    display: inline-block;
    border: 1px solid #85af4b;
  }
  
  .about-me a:hover {
    background-color: white;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    color: #85af4b;
  
  }
  
  .about-me .col-left img {
    max-width: 75%;
  }

  @media only screen and (max-width: 900px) {
    .about-me {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
    }
  }
.my-skills {
  background-color: #e2e2e2;
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
}

.my-skills h2 {
  text-transform: uppercase;
  position: relative;
}

.my-skills h2:after {
  content: "";
  position: absolute;
  width: 80px;
  height: 2px;
  margin-left: -40px;
  left: 50%;
  bottom: 0px;
  background-color: #85af4b;
}

.my-skills:after {
  content: "";
  display: block;
  clear: both;
}

.my-skills .skill-item-1,
.my-skills .skill-item-2,
.my-skills .skill-item-3,
.my-skills .skill-item-4,
.my-skills .skill-item-5,
.my-skills .skill-item-6 {
  width: 20%;
  display: inline-block;
  margin: 20px;
}

.my-skills i {
  font-size: 80px;
  color: #85af4b;
}

.my-projects {
    background-color: white;
    padding-top: 50px;
    padding-bottom: 50px;
    text-align: center;
  }
  
  .my-projects:after {
    content: '';
    display: block;
    clear: both;
  }
  
  .my-projects h2 {
    text-transform: uppercase;
    margin-top: 0;
    position: relative;
  }
  
  .my-projects h2:after {
    content: '';
    position: absolute;
    width: 80px;
    height: 2px;
    margin-left: -40px;
    left: 50%;
    bottom: 0px;
    background-color: #85af4b
  }
  
  .my-projects .project1, .my-projects .project2, .my-projects .project3 {
    width: 30%;
    height: 300px;
    float: left;
    background-color: #85af4b;
    position: relative;
    overflow: hidden;
  }
  
  .my-projects .project1 {
    margin-left: 2%;
    margin-right: 1.5%;
    background-image: url(/portfolio/static/media/Kodflix.a3e3673b.png);
    background-size: contain;
  }
  
  .my-projects .project2 {
    margin-left: 1%;
    margin-right: 1%;
    background-image: url(/portfolio/static/media/Koinalysis.da30b969.png);
    background-size: contain;
  }
  
  .my-projects .project3 {
    margin-left: 1.5%;
    margin-right: 2%;
    background-image: url(/portfolio/static/media/code.692a57eb.jpg);
    background-size: contain;
  }
  
  .my-projects .overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    padding-top: 310px;
  }
  
  .my-projects .overlay:hover {
    background-color: rgba(0, 0, 0, 0.6);
    padding-top: 130px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  
  .overlay a {
    display: inline-block;
    font-size: 18px;
    padding: 10px, 20px;
    border: 1px solid #fff;
    text-decoration: none;
    color: #fff;
    font-weight: bold;
  }

  @media only screen and (max-width: 60px) {
    .component {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
    }
  }
.contact-section {
  text-align: center;
  background-color: #e2e2e2;
  padding-top: 50px;
  padding-bottom: 50px;
}

.contact-section:after {
  content: "";
  display: block;
  clear: both;
}

.contact-section h2 {
  text-transform: uppercase;
  margin-top: 0;
  position: relative;
}

.contact-section h2:after {
  content: "";
  position: absolute;
  width: 80px;
  height: 2px;
  margin-left: -40px;
  left: 50%;
  bottom: 0px;
  background-color: #85af4b;
}

.social-container {
  width: 80%;
  margin: 0 auto;
  text-align: left;
}

.social-container i {
  float: left;
  margin-top: 17px;
  margin-right: 20px;
  font-size: 24px;
  color: #85af4b;
}

.col-1 p,
.col-2 p,
.col-3 p {
  display: inline-block;
}

.col-1,
.col-2,
.col-3 {
  width: 33.333333%;
  display: inline-block;
  float: left;
  text-decoration: none;
}

.col-3 p {
  margin-bottom: 0;
  margin-top: 1px;
  padding-top: 100px;
}

.text-bold {
  color: #333;
  font-weight: bold;
  text-decoration: none;
}

