* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Open Sans', sans-serif
}

h4 {
  font-size: 21px;
}

h4, h5, h6 {
  margin-top: 10px;
  margin-bottom: 10px;
}

body {
  margin: 0;
  padding: 0;
  color: #777;
  width: 100%;
  height: 100%;
  font-size: 17px;
  line-height: 1.7;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;

}


